import { bus, isWeixinBrowser } from '../config/util'

export default class MusicManager {
	constructor (list) {
		this.list = list
		list.forEach((v, i) => {
			if (bus.musicList[v.name]) {
				console.log('MusicManager: 音乐id已存在')
				return
			}
			bus.musicList[v.name] = new Audio()

			if (v.loop) {
				bus.musicList[v.name].loop = true
			}

			if (isWeixinBrowser()) {
				document.addEventListener('WeixinJSBridgeReady', () => {
					console.log('audio WeixinJSBridgeReady')
					bus.musicList[v.name].play()
					if (!v.autoplay) {
						bus.musicList[v.name].pause()
					}
				}, false)
				bus.musicList[v.name].addEventListener('canplaythrough', () => {
					console.log('audio canplaythrough')
				}, false)
			} else {
				bus.musicList[v.name].addEventListener('canplaythrough', () => {
                    console.log('audio canplaythrough')
                    bus.musicList[v.name].pause()
					bus.musicList[v.name].play()
					if (!v.autoplay) {
						bus.musicList[v.name].pause()
					}
				}, false)
			}

			bus.musicList[v.name].src = v.src
		})
	}

	play (name) {
		// 自动播放时，不能连续播放。是loop的话，Android和chrome不会loop
		//
		if (bus.musicList[name] && bus.musicList[name].paused) {
			bus.musicList[name].play()
		}
	}
	playHard (name) {
		this.stop(name)
		setTimeout(() => {
			this.play(name)
		}, 10)
	}

	resume (name) {
		if (bus.musicList[name] && bus.musicList[name].paused) {
			bus.musicList[name].play()
		}
	}
	pause (name) {
		if (bus.musicList[name] && !bus.musicList[name].paused) {
			bus.musicList[name].pause()
		}
	}
	pauseAll () {

	}
	stop (name) {
		if (bus.musicList[name]) {
			bus.musicList[name].pause()
			bus.musicList[name].currentTime = 0
		}
	}
}



